body, html {
  height: 100%;
}

body {
  line-height: 1;
  color: #000;
  text-align: left;
  z-index: 1;
  position: relative;
}

input [type=text] {
  padding: 0;
}

textarea {
  resize: none;
  overflow: auto;
}

button {
  cursor: pointer;
}

input {
  &[type=button], &[type=submit] {
    cursor: pointer;
  }
}

label {
  cursor: pointer;
}

button {
  -webkit-appearance: none;
}

input {
  &[type=button], &[type=email], &[type=number], &[type=password], &[type=phone], &[type=reset], &[type=submit], &[type=tel], &[type=text] {
    -webkit-appearance: none;
  }
}

textarea {
  -webkit-appearance: none;
}

a {
  text-decoration: none;
  &:active, &:focus, &:hover, &:visited {
    text-decoration: none;
  }
}

button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

input {
  &[type=submit]::-moz-focus-inner {
    border: 0;
    padding: 0;
  }
  &:invalid {
    outline: 0;
    -moz-box-shadow: none;
    border: none;
  }
}

::-ms-clear {
  display: none;
}

:focus {
  outline: 0;
}

input {
  outline: 0;
  &[type=search]:focus {
    outline: 0;
  }
}

textarea {
  outline: 0;
}

