@-webkit-keyframes fade-in-2 {
  0%,10% {
    opacity: 0;
    -webkit-filter: blur(8px);
    filter: blur(8px);
    letter-spacing: 10px;
  }

  100%,70% {
    opacity: 1;
    -webkit-filter: blur(0);
    filter: blur(0);
    letter-spacing: initial;
  }
}


@keyframes fade-in-2 {
  0%,10% {
    opacity: 0;
    -webkit-filter: blur(8px);
    filter: blur(8px);
    letter-spacing: 10px;
  }

  100%,70% {
    opacity: 1;
    -webkit-filter: blur(0);
    filter: blur(0);
    letter-spacing: initial;
  }
}


@-webkit-keyframes fade-out-2 {
  0% {
    opacity: 1;
    -webkit-filter: blur(0);
    filter: blur(0);
    letter-spacing: initial;
  }

  100% {
    opacity: 0;
    -webkit-filter: blur(8px);
    filter: blur(8px);
    letter-spacing: 10px;
  }
}


@keyframes fade-out-2 {
  0% {
    opacity: 1;
    -webkit-filter: blur(0);
    filter: blur(0);
    letter-spacing: initial;
  }

  100% {
    opacity: 0;
    -webkit-filter: blur(8px);
    filter: blur(8px);
    letter-spacing: 10px;
  }
}


@-webkit-keyframes fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}


@keyframes fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}


@-webkit-keyframes fade-out {
  from {
    opacity: 1;
  }

  to {
