.loader-logo {
  width: 75px;
  height: 75px;
  position: relative;
  .content {
    width: 100%;
    height: 100%;
    background: #000;
  }
  .bottom, .top {
    width: 100%;
    height: 100%;
    position: absolute;
    overflow: hidden;
  }
  .left-arrow {
    .top {
      -webkit-clip-path: url(#left-top);
      clip-path: url(#left-top);
      .content {
        -webkit-animation: 3s ease-in-out infinite forwards in;
        animation: 3s ease-in-out infinite forwards in;
      }
    }
    .bottom {
      -webkit-clip-path: url(#left-bottom);
      clip-path: url(#left-bottom);
      .content {
        margin-left: -100%;
        -webkit-animation: 3s ease-in-out 1.2s infinite forwards out;
        animation: 3s ease-in-out 1.2s infinite forwards out;
      }
    }
  }
  .right-arrow {
    .top {
      -webkit-clip-path: url(#right-top);
      clip-path: url(#right-top);
      .content {
        margin-left: 100%;
        -webkit-animation: 3s ease-in-out 1.2s infinite forwards in;
        animation: 3s ease-in-out 1.2s infinite forwards in;
      }
    }
    .bottom {
      -webkit-clip-path: url(#right-bottom);
      clip-path: url(#right-bottom);
      .content {
        -webkit-animation: 3s ease-in-out infinite forwards out;
        animation: 3s ease-in-out infinite forwards out;
      }
    }
  }
}

.logo {
  display: block;
  position: relative;
  overflow: hidden;
  &:hover .loader-logo {
    opacity: 1;
    -webkit-animation-play-state: running;
    animation-play-state: running;
  }
}

.logo__main, .logo__sticky {
  display: block;
  transition: opacity .25s linear;
}

.logo__main > svg, .logo__sticky > svg {
  width: 115px;
  height: auto;
}

.logo__main > img {
  display: block;
  width: 115px;
  height: auto;
}

.logo__sticky {
  > img {
    display: block;
    width: 115px;
    height: auto;
  }
  opacity: 0;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  img {
    max-width: 60px;
    max-height: 100%;
    width: auto;
    display: block;
    transition: opacity .3s linear;
  }
  &:hover img {
    opacity: 0;
  }
}

.logo .loader-logo {
  background: #000;
  display: block;
  transition: opacity .25s linear;
  position: absolute;
  width: 30px;
  height: 30px;
  top: 2px;
  left: 0;
  opacity: 0;
  -webkit-animation-play-state: initial;
  animation-play-state: initial;
  span {
    display: block;
  }
  .content {
    background: #fff;
  }
}

.btn {
  height: 60px;
  border: none;
  padding: 0 32px;
  color: #000;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 11px;
  background: #fff;
  text-align: left;
  letter-spacing: 1.5px;
  text-transform: uppercase;
}

.btn_2 {
  background-color: transparent;
  border: 1px solid #000;
  padding: 0 39px 0 40px;
  letter-spacing: 1.9px;
}

.btn_3 {
  color: #fff;
  background-color: #000;
  border: 1px solid #000;
  padding: 0 39px 0 40px;
  letter-spacing: 1.9px;
}

.btn_4 {
  background-color: transparent;
  border: 1px solid #fff;
  color: #fff;
  padding: 0 30px;
}

@media (min-width: 1260px) {
  .logo {
    svg {
      width: 155px;
    }
    .loader-logo {
      width: 40px;
      height: 40px;
    }
  }
  .btn {
    transition: background-color .15s linear,color .15s linear;
    &:active, &:hover {
      background-color: #000;
      color: #fff;
    }
  }
  .btn_2 {
    &:active, &:hover {
      background-color: #000;
    }
  }
  .btn_3 {
    &:active, &:hover {
      color: #000;
      background-color: #fff;
    }
  }
  .btn_4 {
    &:active, &:hover {
      color: #000;
      background-color: #fff;
    }
  }
}

.arrow {
  height: 51px;
  width: 51px;
  border-radius: 50%;
  display: block;
  background: #000;
  cursor: pointer;
  overflow: hidden;
  -webkit-animation: 3s linear infinite forwards click-me;
  animation: 3s linear infinite forwards click-me;
  span {
    display: flex;
    width: 100%;
    height: 51px;
    -webkit-animation: 3s cubic-bezier(0.6, 0.2, 0.1, 1) infinite forwards click-me-2;
    animation: 3s cubic-bezier(0.6, 0.2, 0.1, 1) infinite forwards click-me-2;
  }
  i {
    display: block;
    position: relative;
    margin: auto;
    height: 22px;
    width: 2px;
    background: #fff;
    &:after, &:before {
      content: '';
      display: block;
      position: absolute;
      bottom: -2px;
      width: 2px;
      height: 9px;
      background: #fff;
      left: -2.5px;
      -webkit-transform: rotate(-45deg);
      transform: rotate(-45deg);
    }
    &:after {
      left: auto;
      right: -3px;
      -webkit-transform: rotate(45deg);
      transform: rotate(45deg);
    }
  }
}

.arrow_white {
  background: #fff;
  i {
    background: #000;
    &:after, &:before {
      background: #000;
    }
  }
}

.arrow {
  &.backwards span {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
    -webkit-animation: 3s cubic-bezier(0.6, 0.2, 0.1, 1) infinite forwards click-me-3;
    animation: 3s cubic-bezier(0.6, 0.2, 0.1, 1) infinite forwards click-me-3;
  }
  &:hover {
    opacity: 1;
  }
}

.clip-svg {
  position: absolute;
  z-index: -999;
  left: 0;
  width: 0;
  height: 0;
}

canvas {
  max-width: 100%;
}